stema regional perteneciente al Mercosur y compuesto por territorios compartidos entre Brasil, Uruguay y Argentina; fue este último país quien apostó fuertemente por aprovechar la gran concentración de valores paisajísticos y culturales del bioma pampeano y en especial de la zona más vinculada al litoral costero entre el mar argentino en el océano atlántico y la desembocadura del Río de la Plata, el cual vivía hacia el año de 1995 una compleja situación con sus ecosistemas naturales.")]),_c('p',{staticClass:"text-regular bloque-texto-f__texto"},[_vm._v("Este delicado equilibrio se veía amenazado principalmente por la desarticulación de sus poblados, el abandono de sus estaciones de ferrocarril, o las necesidades sociales insatisfechas de sus cascos de estancia; pero también por la escasa valoración de las históricas tecnologías campestres utilizadas en sus labores cotidianas, tan apropiadas para el manejo de pastizales, aguadas, y caminos de tropas; por la difícil transición que suponía la lógica evolución tecnológica de sus procesos productivos tras la paulatina introducción del alambrado, el molino, la agricultura tradicionalmente extractiva, hasta llegar al actual y obligado modelo de agroindustria sostenible.")]),_c('p',{staticClass:"text-regular bloque-texto-f__texto"},[_vm._v("Acorde con la problemática, la puesta en marcha del corredor turístico Camino del Gaucho significa en la actualidad una contribución a la conservación y evolución del paisaje, y a la ordenación y desarrollo del territorio, desde una mirada eminentemente cultural: el territorio como paisaje o el arte del paisaje al servicio del territorio”")]),_c('div',{staticClass:"bloque-texto-f__autor mb-3"},[_c('h4',{staticClass:"mb-0"},[_vm._v("(Bernárdez, 2012).")])])])]),_c('div',{staticClass:"col-lg-auto",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"350px"},attrs:{"src":require("@/assets/curso/tema8/img5.png")}})])])])},function () {var _vm=thi